/**
 * Lists get specific experiment of specific e-type
 *
 */
export var layerAnatomyDataQuery = {
  from: 0,
  size: 1000,
  query: {
    bool: {
      filter: [{
        bool: {
          should: [{
            term: {
              '@type': 'https://neuroshapes.org/LayerThickness'
            }
          }, {
            term: {
              '@type': 'https://neuroshapes.org/NeuronDensity'
            }
          }, {
            term: {
              '@type': 'https://neuroshapes.org/SliceCollection'
            }
          }]
        }
      }]
    }
  }
};
export var electroPhysiologyDataQuery = function electroPhysiologyDataQuery(etype, experiment) {
  if (!etype || !experiment) {
    return null;
  }

  return {
    from: 0,
    size: 100,
    query: {
      bool: {
        filter: [{
          bool: {
            must: [{
              term: {
                '@type': 'https://neuroshapes.org/Trace'
              }
            }]
          }
        }, {
          bool: {
            must: {
              term: {
                'name.raw': experiment
              }
            }
          }
        }, {
          nested: {
            path: 'annotation.hasBody',
            query: {
              bool: {
                filter: {
                  term: {
                    'annotation.hasBody.label.raw': etype
                  }
                }
              }
            }
          }
        }, {
          nested: {
            path: 'distribution',
            query: {
              bool: {
                must: {
                  match: {
                    'distribution.encodingFormat': 'application/nwb'
                  }
                }
              }
            }
          }
        }]
      }
    }
  };
};
