export var etypeTracesDataQuery = function etypeTracesDataQuery(etype) {
  if (!etype) {
    return null;
  }

  return {
    from: 0,
    size: 10000,
    query: {
      bool: {
        filter: [{
          bool: {
            must: [{
              term: {
                '@type': 'https://neuroshapes.org/Trace'
              }
            }]
          }
        }, {
          bool: {
            must_not: {
              exists: {
                "field": "note"
              }
            }
          }
        }, {
          nested: {
            path: 'annotation.hasBody',
            query: {
              bool: {
                filter: {
                  term: {
                    'annotation.hasBody.label.raw': etype
                  }
                }
              }
            }
          }
        }, {
          nested: {
            path: 'distribution',
            query: {
              bool: {
                must: {
                  match: {
                    'distribution.encodingFormat': 'application/rab'
                  }
                }
              }
            }
          }
        }]
      }
    }
  };
};